footer{background:#ebe8e8;width:100%;display:grid;grid-template-columns:1fr 1fr}footer a{padding:2px 8px}footer a:hover{background:#000;border-radius:15px;color:white}footer ol{list-style:none;display:flex;flex-direction:row}footer ol li{margin:0 12px}footer img{width:auto}footer .icon{color:#ececec}footer .icon:hover{color:white}footer p{padding-left:20px}header{width:90vw;margin:auto;display:flex;flex-direction:row;justify-content:space-between;align-items:center;height:40px;padding-top:20px}header img{height:30px;width:auto}.body{background:blue;overflow-x:hidden}.App{font-family:'Noto Sans', sans-serif;display:grid;grid-template-rows:50px 1fr 50px}header,footer{height:50px}main{min-height:calc(100vh - 100px)}#title{text-align:center;font-size:30px}a{text-decoration:inherit;font-weight:bolder;color:inherit}.shopping_list{display:grid;grid-template-rows:200px 80px 50px 5px 1fr 50px 50px;align-items:center;justify-items:center;margin-bottom:30px}.shopping_list .img-main{height:200px;width:auto}.shopping_list h1{font-weight:900;padding-bottom:40px}.shopping_list ol{list-style:none;margin:0;padding:0}.shopping_list .legend{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:90vw;display:grid;grid-template-columns:10px 90px 1fr 70px 150px;align-items:center;justify-items:center;font-weight:700}.shopping_list .buttons{display:flex;flex-direction:row;justify-content:center;align-items:center;width:80%}.shopping_list .hr{background:#ccc;width:90vw;height:1px;margin-bottom:20px}.filter{width:92%;display:flex;flex-direction:row;align-items:center;justify-content:flex-end}.filter p{margin:0 12px}.filter input{cursor:pointer;-webkit-appearance:none;position:relative;width:50px;height:25px;background-size:cover;border-radius:50px;outline:none;transition:background-image .3s;box-shadow:0px 2px 5px 1px #d8d8d8}.filter input:before{content:"";position:absolute;top:0;left:0;height:25px;width:25px;border-radius:50px;transition:all .3s;background-color:#4952a3}.filter input:checked{background:#4952a3;transition:background-image .3s}.filter input:checked:before{-webkit-transform:translate(100%);transform:translate(100%);transition:all .3s;background-color:#dfdfdf}.overlay form{display:grid;grid-template-columns:300px 100px 100px;grid-gap:5px}.overlay form input[type="text"]{background:#e0dfdf;border:1px solid #c5c5c5;border-radius:20px;padding-left:15px}.overlay-dropzone div{cursor:pointer;padding:20px;margin:0;border-radius:20px;border:2px dashed silver;background:none}.overlay-dropzone div:hover{background-color:#ebebeb}.overlay-dropzone form{display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.overlay-dropzone .dropzone{margin:20px;background:#fbfbfb;border-radius:15px;border-style:inset;border:2px #bbb dashed;height:20vh;width:50vw;color:#bbb;display:grid;align-items:center;justify-content:center}.overlay-dropzone .dropzone:hover{background:white}.btn{-webkit-transform:scale(0.9);transform:scale(0.9);background:none;border:none;cursor:pointer}.btn-roll:hover{-webkit-transform:scale(0.9) translateY(-2px) rotate(-180deg);transform:scale(0.9) translateY(-2px) rotate(-180deg);transition:ease .3s}.btn-delete{color:#dd8585}.btn-delete:hover{color:#ec3e3e}.btn-edit{color:#adadad}.btn-edit:hover{color:#797878}.btn-oval{width:200px}.btn-submit{background:#14357e;border:2px #14357e solid;color:white;padding:10px;border-radius:30px;font-weight:800}.btn-submit:hover{-webkit-transform:scale(0.95);transform:scale(0.95);transition:ease .3s}.btn-clear{color:#e6b6b6;padding:10px 20px;border:2px #e6b6b6 solid;border-radius:30px;font-weight:800}.btn-clear:hover{-webkit-transform:scale(0.95);transform:scale(0.95);transition:ease .3s}.checkbox{-webkit-transform:scale(1.7);transform:scale(1.7)}.btn-drawer{color:#6d6c6c}.item{width:90vw;height:75px;background:#f7f6f6;box-shadow:2px 2px 4px #f5f5f5;display:grid;grid-template-columns:8px 1fr 60px;align-items:center;justify-items:center;margin:6px 0;padding:0px 0